export default class ObjectExpression {
    constructor(props) {
        this.expression = {
            type: "ObjectExpression",
            properties: props ? props : []
        };
    }
    addKeyValue(key, value) {
        this.expression.properties.push(
            {
                "type": "Property",
                "key": {
                    "type": "Literal",
                    "value": key,
                    "raw": "\"" + key + "\""
                },
                "value": value,
                "kind": "init",
                "method": false,
                "shorthand": false
            }
        );
    }
}